const API_URL = "https://awcu2nks23.us-east-1.awsapprunner.com";
const load = async ({ params }) => {
  const response = await fetch(`${API_URL}/api/${params.event}/seeding`);
  if (response.status == 404) {
    return {
      seeding: null
    };
  }
  const data = await response.json();
  return {
    seeding: data
  };
};
export {
  load
};
